body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.layout {
  width: 85%;
  margin: 0px auto;

}
.header{
  display: flex;
  height: 132px;
  justify-content: center;
  align-items: center;
  list-style: none;
  border-bottom: 3px solid black;
  margin-bottom: 35px;
}
.header .logo{
  flex-grow:2;
  display:flex;

}
.header .logo span{
  display: block;
  font-size: 90px;
  font-weight: bolder;
  margin-top: -8px;
}
.header .logo h3{
  display:block;
  flex-basis: 40px;
  margin-left:10px ;
  font-weight: 400;
  text-transform: uppercase;
}

.header nav{
  flex-grow:1 ;
  
}
 
.header nav ul{
  height: 132px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}

.header li{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 16px;
  margin-left: 15px;
  margin-right: 15px;
  
}
.header li a{
  color:black;
  text-decoration: none;
  transition: all 300ms;
}

.header a:hover{
  color:#fa4559
}
.footer{
  min-height: 40px;
  line-height: 7px;
  text-align: center;
  font-size: 14px;
  margin-top: 80px;
  border-top: 3px solid black;
  padding-top: 45px;
  background: #f5f5f5;
  color: #444;
  
}
.content{
  min-height: 400px;
}
.header .active,
.header a:hover{
  color:#fa4559;
}


.home h1{
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: 400;
  font-size: 60px;
  margin-bottom: 80px;
  
}
.home strong{
  color:#fa4559;

}
.home h2{
  color:rgb(98, 97, 97);
  font-weight: lighter;
}
.home .title a{
  padding: 7px;
  border-radius: 5px;
  color: white;
  background-color: #fa4559;
  text-decoration: none;
  transition: all 300ms ;
}

.page .heading,
.home .heading{
  font-size: 40px;
  font-style: italic;
  font-weight: 500;
  margin-bottom: 0px;
  color: black;
}
.page .heading{
  font-size: 55px;
  font-weight: bold;
}
.home p{
  color:rgb(98, 97, 97);

}
.home a,
input[type="text"],
textarea{
  
   display: block;
   padding: 15px;
   margin-top: 25px;
   width: 250px;
}
input[type="submit"],
button{
  font-size: 15px;
   padding: 15px;
   border: 0px;
   margin-top: 15px;
   border-radius: 5px;
   color: white;
   background-color: #fa4559;
   text-decoration: none;
   transition: all 300ms;

}
.home .title a:hover,
input[type="submit"]:hover,
button:hover{ 
  background-color:rgb(26, 137, 26);
 
 }
 .works{
  display: flex;
  flex-wrap: wrap;
 }
 .work-item{
  width: 30%;
  flex-grow: 1;
  margin: 30px;

 }
 .work-item .mask{
  width: 100%;
  height: 350px;
  overflow: hidden;
  border: 1px solid #ddd;
  box-shadow: 0px 5px 15px #ddd;
 }
 
 .work-item img{
   width: 78%;
   transition: 500ms;
 }
 .work-item .mask:hover img{
  width: 100%;
  margin-right: 30%;
 }

 .work-item span{
  display: block;
  color: gray;
  margin-top: 35px;
 }
 .work-item h2{
  display: block;
  font-size: 30px;
  color: black;
  margin-top: 5px;
  margin-bottom: 10px;
 }
 .work-item a{
  color: black;
  text-decoration: none;
  transition: 300ms;
 }

 .work-item a:hover{
  color: #fa4559;
 }

 .work-item h3{
  display: block;
  color: gray;
  border-top: 1px solid #ddd;
  padding-top: 15px;
  margin-top: 0px;
 }

 .page-work .mask{
  height: 300px;
  overflow: hidden;
  box-shadow: 0px 0px 10px #ccc;
 }





